.contact-line-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#contact-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: space-around;
  margin: 0.3rem 0;
  width: 100%;
}
label {
  margin-top: 1vmin;
  font-size: 3vmin;
}
input {
  margin-top: 1vmin;
  font-size: 3vmin;
}
h4 {
  font-size: 3vmin;
}
